@use "sass:map";
@use "." as this;
@use ".." as super;
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/AmountFormatter";
@use "@app/bancor-components/Style/size";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";

#{this.$selector} {
    .buttonMask {
        @include Color.set($bg: "transparent");
        @include Border.set($style: none);
        @include Flex.set($align: center);
        cursor: pointer;
    }

    .account,
    .credit_card,
    .loan {
        @include Flex.set($justify: between, $dir: column);
        @include super.textBody;
        @include super.cardMobile;
        @include super.cardDesktop;

        & > header {
            @include Flex.set($justify: "between");
            @include Font.set($size: gamma, $weight: default, $family: "default");

            & > div {
                @include Flex.set($align: center);
            }

            .dot {
                @include Margin.set($left: alpha);
            }
        }

        & > section {
            @include Font.set($size: delta, $weight: default, $family: "heading");

            #{AmountFormatter.$selector} {
                span,
                data,
                #{Text.$selector} {
                    @include Color.set($fg: "white");
                    @include Font.set($size: iota, $weight: "bold", $family: "heading");
                }
            }
        }

        & > footer {
            @include Flex.set($justify: between, $align: center);

            #{Button.$selector} {
                @include Color.set($bg: "transparent", $border: "transparent");
                @include Flex.set($justify: center, $align: center);
                @include Border.set($style: default);
                @include Padding.set($preset: none);
                cursor: pointer;
            }

            .primaryTextLink,
            .secondaryTextLink {
                @include Font.set($size: gamma, $weight: medium, $family: "default");
                @include Color.set($fg: "white");
            }
        }
    }

    .account {
        @include Color.set($bg: "white");

        #{Text.$selector}.textHeader {
            @include Color.set($fg: "gray-dark");
        }
        #{Text.$selector}.dot {
            @include Color.set($fg: "primary");
            @include Margin.set($right: alpha);
        }

        & > section {
            @include Font.set($size: delta, $weight: default, $family: "heading");

            #{AmountFormatter.$selector} {
                span,
                data,
                #{Text.$selector} {
                    @include Color.set($fg: "gray-dark");
                }
            }
        }

        #{Button.$selector} {
            .primaryTextLink,
            .secondaryTextLink {
                @include Color.set($fg: "primary");
            }

            path {
                @include Color.set($svg-fg: "primary");
            }
        }

        & > footer {
            @include Margin.set($top: auto);
        }
    }

    .credit_card {
        #{Text.$selector} {
            @include Color.set($fg: "white");
        }

        & > header {
            .textHeader {
                @include Margin.set($left: alpha);
            }
        }
    }

    .credit_card.cardColor_1 {
        background: var(--card-background-1);
    }
    .credit_card.cardColor_2 {
        background: var(--card-background-2);
    }
    .credit_card.cardColor_3 {
        background: var(--card-background-3);
    }
    .credit_card.cardColor_4 {
        background: var(--card-background-4);
    }
    .credit_card.cardColor_5 {
        background: var(--card-background-5);
    }
    .credit_card.cardColor_6 {
        background: var(--card-background-6);
    }
    .credit_card.cardColor_7 {
        background: var(--card-background-7);
    }
    .credit_card.cardColor_8 {
        background: var(--card-background-8);
    }
    .credit_card.cardColor_9 {
        background: var(--card-background-9);
    }
    .credit_card.cardColor_10 {
        background: var(--card-background-10);
    }
    .credit_card.cardColor_11 {
        background: var(--card-background-11);
    }
    .credit_card.cardColor_12 {
        background: var(--card-background-12);
    }

    .loan {
        @include Color.set($bg: "blue-800");
        #{Text.$selector}.textHeaderTwo {
            @include Font.set($transform: lower);
        }
        .textHeaderTwo::first-letter {
            @include Font.set($transform: caps);
        }
        #{Text.$selector} {
            @include Color.set($fg: "white");
        }
        #{Text.$selector}.dot {
            @include Margin.set($right: alpha);
        }
    }
    
    .ariaHiddenText {
        position: absolute;
        overflow: hidden;
        width: 0;
        height: 0;
      }
}
