@use "." as this;
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout/Margin";

#{this.$selector} {
    .option {
        @include Flex.set($align: center);
        width: 80%;
    }

    .labelOptionsText {
        @include Font.set($size: delta, $weight: default, $family: "default", $style: default, $space: "nowrap");
        @include Margin.set($left: alpha);
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .avatar {
        @include Flex.set($shrink: none);
    }

    @include screen.md {
        .option {
            width: 100%;
        }
    }
}
